import React, { useState } from "react";
import { connect, FormikActions } from "formik";
import * as Yup from "yup"
import FormikContainer from "../../components/formikContainer/FormikContainer";
import NewExpense from "./componets/NewExpense";
import { Expense } from "../../models/Expense";
import { LinearProgress } from "@material-ui/core";


export const CreateNewExpense: React.SFC<{}> = () => {

    const defaulValue: Expense = { urlImage: "", amount: 0, description: "", location: { latitude: 0, longitude: 0 }, dateBuy: new Date() }

    const [expense, setExpense] = useState<Expense>(defaulValue)

    const validationSchema = Yup.object().shape<Expense | any>({
        description: Yup.string().nullable(),
        urlImage: Yup.string().nullable(),
        amount: Yup.number().min(1,"Insira o valor total").required("Insira o valor total"),
        dateBuy: Yup.date().nullable().required("Informe a data"),

        locationDesciption: Yup.string().required("Localização invalida")
    })

    function onSubmit(item: Expense, formikActions: FormikActions<Expense>) {

        console.log(item);
        console.log(formikActions);

        setTimeout(() => {
            formikActions.setSubmitting(false);
        }, 4000);

    }


    return (
        <FormikContainer
            validationSchema={validationSchema}
            initialValues={{ ...expense, locationDesciption: "" }}
            onSubmit={onSubmit}
        >
            <NewExpense />

        </FormikContainer >
    );
}

export default connect(CreateNewExpense);