import React, { useEffect } from "react";
import { Field, connect, FormikContext } from "formik";
import { TextField } from 'formik-material-ui';
import { Grid, Button } from "@material-ui/core";
import GetImage from "./GetImage";
import { Location } from './../../../models/Expense';
import { KeyboardDatePicker } from '@material-ui/pickers';

interface Props {
    formik: FormikContext<any>;
}

export const NewExpense: React.SFC<Props> = (props) => {



    function getLocation() {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(showPosition);
        } else {
            console.log("Geo Location not supported by browser");
        }
    }

    function showPosition(position: any) {
        let location: Location = {
            longitude: position.coords.longitude,
            latitude: position.coords.latitude
        };

        props.formik.setFieldValue("location", location);
    }

    useEffect(() => {
        getLocation();
    }, [])

    useEffect(() => {
        props.formik.setFieldValue("locationDesciption", props.formik.values.location.latitude + " " + props.formik.values.location.longitude);

    }, [props.formik.values.location])

    return (

        <Grid container direction="row" justify="flex-start" spacing={2}>

            <Grid item xs={12}>

                <GetImage disabled={props.formik.isSubmitting} />

            </Grid>

            <Grid item xs={12}>
                <Field
                    name="amount"
                    label="Valor Total"
                    type="number"
                    component={TextField}
                    margin="normal"
                    fullWidth />

            </Grid>

            <Grid item xs={12}>
                <Field
                    validation={"false"}
                    name="locationDesciption"
                    label="Localização"
                    type="text"
                    component={TextField}
                    margin="normal"
                    fullWidth />

            </Grid>

            <Grid item xs={12}>
                <KeyboardDatePicker
                    disabled={props.formik.isSubmitting}
                    disableToolbar
                    error={props.formik.errors.dateBuy != undefined}
                    helperText={props.formik.errors.dateBuy}
                    autoOk
                    label="Data da compra"
                    variant="inline"
                    name="dateBuy"
                    value={props.formik.values.dateBuy}
                    format="dd/MM/yyyy"
                    onChange={date => { props.formik.setFieldValue("dateBuy", date) }}
                    fullWidth />
            </Grid>

            <Grid item xs={12}>
                <Field
                    name="description"
                    label="Descrição"
                    component={TextField}
                    margin="normal"
                    multiline={true}
                    rows="4"
                    fullWidth />

            </Grid>

            <Grid item xs={12} container direction="row" justify="flex-end" alignItems="flex-end">

                <Button type="submit" variant="contained" color="primary" style={{ marginTop: "20px" }} >
                    Salvar
                </Button>

            </Grid>

        </Grid>

    );
}

export default connect<{}, {}>(NewExpense);
