import styled from "styled-components";

export const ImageConteiner = styled.figure`
    position: relative;
    margin: 0;
`

export const Image = styled.img`
    width: 100%;
    height: 100%;
    border: 1px solid;
`

export const FigCaptionLegend = styled.figcaption`
    position: absolute;
    top: 90%;
    left: 50%;
    transform: translate(-50%,-50%);
`