import React from "react";
import clsx from "clsx";
import IconButton from "@material-ui/core/IconButton";
import { Divider, ListItem, ListItemIcon, ListItemText, List } from "@material-ui/core";
import Drawer from '@material-ui/core/Drawer';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import { useTheme } from '@material-ui/core/styles';
import { Redirect, RouteComponentProps, withRouter } from "react-router-dom";


interface Props extends RouteComponentProps<any> {
    classes: any;
    open: boolean;
    handleDrawerClose: () => void;
}

interface MenuOption {
    text: string,
    icon: JSX.Element,
    url: string;
}

export const SiderDrawer: React.SFC<Props> = (props) => {

    const theme = useTheme();
    const MenuOption: MenuOption[] = [{ text: "Home", icon: <InboxIcon />, url: "DashBoard" }, { text: "Novo Gasto", icon: <MailIcon />, url: "CreateNewExpense" }];


    return (
        <Drawer style={{ display: "contents" }}
            variant="permanent"
            className={clsx(props.classes.drawer, {
                [props.classes.drawerOpen]: props.open,
                [props.classes.drawerClose]: !props.open,
            })}
            classes={{
                paper: clsx({
                    [props.classes.drawerOpen]: props.open,
                    [props.classes.drawerClose]: !props.open,
                }),
            }}
            open={props.open}>

            <div className={props.classes.toolbar}>
                <IconButton onClick={props.handleDrawerClose}>
                    {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
                </IconButton>
            </div>

            <Divider />

            <List>
                {MenuOption.map((item, index) => (
                    <ListItem button key={index} onClick={() => { props.history.push(item.url); props.handleDrawerClose() }} >
                        <ListItemIcon>{item.icon}</ListItemIcon>
                        <ListItemText primary={item.text} />
                    </ListItem>
                ))}
            </List>

        </Drawer>

    );
}


export default withRouter(SiderDrawer);