import React from 'react';
import Camera, { FACING_MODES, IMAGE_TYPES } from 'react-html5-camera-photo';
import 'react-html5-camera-photo/build/css/index.css';
import { useState } from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import { setTimeout } from 'timers';

//https://www.npmjs.com/package/react-html5-camera-photo
// export class CameraFoto extends Component {
export const CameraFoto = (props) => {

  const [load, setLoad] = useState(true)

  function onTakePhoto(dataUri) {
    setLoad(true);

    if (props.onTakePhoto) {
      props.onTakePhoto(dataUri);
    }
  }

  function onCameraError() {
    setLoad(false);

    if (props.onCameraError) {
      props.onCameraError();
    }
  }

  function onCameraStart() {

    setTimeout(() => {
      setLoad(false);
    }, 300);

  }

  function onCameraStop() {
    setLoad(true);
  }


  function renderLoadCamera() {
    return (
      load ?
        <div style={{ paddingBottom: "300px" }}>
          <div style={{ paddingTop: "200px", position: "absolute", width: "100%", textAlign: "center" }}>

            <CircularProgress style={{ fontSize: "24px" }} />
            <p style={{ fontSize: "24px" }} >AGUARDE!!</p>

          </div>
        </div>
        : null
    );
  }

  return (
    <>

      {/* position: static|absolute|fixed|relative|sticky|initial|inherit; */}
      {renderLoadCamera()}
      <div >
        <Camera
          onTakePhoto={onTakePhoto}
          onCameraError={onCameraError}
          idealFacingMode={FACING_MODES.ENVIRONMENT}
          // idealResolution = {{height: 1080, width: 1920 }}
          imageType={IMAGE_TYPES.JPG}
          imageCompression={1}
          // isMaxResolution={false}
          isImageMirror={false}
          isSilentMode={true}
          isDisplayStartCameraError={true}
          isFullscreen={true}
          sizeFactor={1}
          onCameraStart={onCameraStart}
          onCameraStop={onCameraStop}
        />
      </div>
    </>
  );
}


export default CameraFoto;