import React, { useState, useContext, useEffect } from "react";
import CameraFoto from "../../../components/camera/CameraFoto";
import { ImageConteiner, FigCaptionLegend, Image } from "../CreateNewExpenseStyle";
import { Cached } from '@material-ui/icons';
import HeaderContext, { HeaderContextProp } from "../../../layout/HeaderContext";
import { Zoom } from "@material-ui/core";


interface Props {
    disabled: boolean;
}

export const GetImage: React.SFC<Props> = (props) => {

    const [cameraVisible, setCameraVisible] = useState<boolean>(true)
    const [uriImage, setUriImage] = useState<string>("");
    const { headerVisible, setHeaderVisible } = useContext<HeaderContextProp>(HeaderContext)

    useEffect(() => {
        setHeaderVisible(false);

        return () => {
            setHeaderVisible(true);
        };
    }, [])

    function onTakePhoto(dataUri: string) {
        setCameraVisible(false);
        setUriImage(dataUri);
        setHeaderVisible(true);
    }

    function onCameraError() {
        setCameraVisible(false);
        setHeaderVisible(true)
        console.log("Deu Errrorororororoororrooro")
    }

    // style={{width:"52px", height:"52px" }} viewBox="0 0 24 24" onClick={() => alert("Oi")}
    // <path fill="#000000" d="M2 12C2 16.97 6.03 21 11 21C13.39 21 15.68 20.06 17.4 18.4L15.9 16.9C14.63 18.25 12.86 19 11 19C4.76 19 1.64 11.46 6.05 7.05C10.46 2.64 18 5.77 18 12H15L19 16H19.1L23 12H20C20 7.03 15.97 3 11 3C6.03 3 2 7.03 2 12Z" />
    function renderImageOptions() {

        if (props.disabled) {
            return null;
        }

        return (
            <Cached color="error" style={{ fontSize: "50px" }} onClick={() => {
                setCameraVisible(true);
                setHeaderVisible(false)
            }} />
        );
    }

    return (
        cameraVisible ?
            <CameraFoto onTakePhoto={onTakePhoto} onCameraError={onCameraError} />
            :
            <Zoom in={true}>
                <ImageConteiner>
                    <label className="MuiFormLabel-root MuiInputLabel-root " style={{
                        transform: "translate(0, 1.5px) scale(0.75)",
                        transformOrigin: "top left",
                    }} >Nota</label>
                    <Image src={uriImage} ></Image>
                    <FigCaptionLegend >
                        {renderImageOptions()}
                    </FigCaptionLegend >

                </ImageConteiner>
            </Zoom>
    );
}

export default GetImage;