import React, { useContext, useState } from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import { Header } from './component/Header';
import SiderDrawer from './component/SiderDrawer';
import { useStyles } from './SidebarStyle';
import HeaderContext, { HeaderContextProp } from '../../layout/HeaderContext';
import Button from '@material-ui/core/Button';
import { Alert } from 'react-native';
var prevX = -1;

interface Props {
  children?: JSX.Element[] | JSX.Element
}

// export default function Sidebar() {
export const Sidebar: React.SFC<Props> = (props) => {

  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [clientX, setClientX] = useState<number>(0)
  const [clientY, setClientY] = useState<number>(0)
  const [showHeader, setShowHeader] = useState<boolean>(true)

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const handleTouchStart = (event: any) => {
    //Data for open/close menu
    setClientX(event.touches[0].clientX);

    //visible the header
    setClientY(event.touches[0].clientY);


  }

  const handleTouchMove = (event: any) => {
    //Data for open/close menu
    let lastClientX = event.touches[0].clientX;
    if (Math.abs(lastClientX - clientX) > 100) {

      if (lastClientX > clientX) {
        setOpen(true);
        setShowHeader(true);

      } else {
        setOpen(false);
      }

    }


    //mudar para o valor do scroll
    //visible the header
    let lasClientY = event.touches[0].clientY;
    if (Math.abs(lasClientY - clientY) > 30) {
      if (lastClientX > clientX) {
        setShowHeader(false);
      } else {
        setShowHeader(true);
      }
    }

    if (event.touches[0].pageY < 800) {
      setShowHeader(true);
    }

  }

  const { headerVisible, setHeaderVisible, formLoading } = useContext<HeaderContextProp>(HeaderContext)

  return (

    <div
      className={classes.root}
      onTouchMove={handleTouchMove}
      onTouchStart={handleTouchStart}>

      {/* <CssBaseline /> */}

      <div className={headerVisible && showHeader ? classes.show : classes.hidden}>

        <Header classes={classes} handleDrawerOpen={handleDrawerOpen} open={open} formLoading={formLoading} />
        <SiderDrawer classes={classes} open={open} handleDrawerClose={handleDrawerClose} />

      </div>


      <div className={headerVisible ? classes.content : classes.show}>
        {props.children}
      </div>

    </div>
  );
}