import React, { useContext, useState } from "react";
import { Formik, FormikConfig, Form, FormikProps } from "formik";
import { Zoom, Grid, SnackbarContent, Icon, Snackbar, IconButton } from "@material-ui/core";
import HeaderContext from "../../layout/HeaderContext";
import { Close as CloseIcon, ErrorOutline as ErrorOutlineIcon } from '@material-ui/icons';
import { red } from "@material-ui/core/colors";

interface Props extends FormikConfig<any> {
    children: JSX.Element[] | JSX.Element
}

export const FormikContainer: React.SFC<Props> = (props) => {

    const { setFormLoading } = useContext(HeaderContext)
    const [state, setState] = useState<any>({})


    function hideMessage(key: string) {
        setState({ ...state, [key]: false })
    }

    function renderMessage(renderProps: FormikProps<any>) {



        // if (renderProps.submitCount > 0 && renderProps.isSubmitting && renderProps.isValidating) {

        //     console.log("Entreii")

        //     let tempState = state;
        //     Object.keys(renderProps.errors).map(key => {
        //         return { ...tempState, [key]: true }
        //     });

        //     setState(tempState)
        // }

        if (renderProps.submitCount > 0) {

            return (
                // console.log(renderProps.errors[key])
                Object.keys(renderProps.errors).map(key => {

                    if (state[key] == undefined) {
                        setState({ ...state, [key]: true })
                    }

                    return (
                        <Snackbar
                            key={key}
                            anchorOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                            open={state[key]}
                            autoHideDuration={6000}
                            
                            onClose={() => { }}
                        >

                            <SnackbarContent
                                style={{ backgroundColor: red[600] }}
                                aria-describedby="client-snackbar"
                                message={
                                    <span id="client-snackbar" style={{ display: 'flex', alignItems: 'center' }} >
                                        <ErrorOutlineIcon style={{ paddingRight: "10px" }} />
                                        {renderProps.errors[key]}
                                    </span>
                                }
                                action={[
                                    <IconButton key="close" aria-label="close" color="inherit" onClick={() => { hideMessage(key) }} >
                                        <CloseIcon />
                                    </IconButton>,
                                ]}
                            />
                        </Snackbar>
                    )
                })

            )

        }

        // Object.keys(renderProps.errors).forEach(key => console.log(renderProps.errors[key]))
    }

    return (
        <Formik
            initialValues={props.initialValues}
            onSubmit={props.onSubmit}

            validationSchema={props.validationSchema}
            render={(renderProps) => {
                return (
                    <Zoom in={true}>
                        <Form >

                            {renderMessage(renderProps)}

                            {setFormLoading(renderProps.isSubmitting)}

                            {props.children}

                            <Grid item xs={12}>
                                <div style={{ height: "300px" }}>
                                    &nbsp;
                                </div>
                            </Grid>

                        </Form>
                    </Zoom>
                )
            }} />
    );
}
{/* <div style={{ paddingTop: "200px", position: "absolute", width: "100%", textAlign: "center" }}> */ }


export default FormikContainer;

